<template>
  <div class="footer" id="contacts">
    <div class="container">
      <div class="footer__content">
        <router-link
          class="footer__content-puzzle"
          :to="{ name: 'home', params: { lang: $route.params.lang } }"
          @click="goToTop()"
        >
          <img
            class="footer__content-puzzle-logo"
            src="@/assets/logo.png"
            alt="logo"
          />
          <p
            class="footer__content-puzzle-descr"
            v-html="puzzleDescr[activeLanguage]"
          ></p>
        </router-link>
        <div class="footer__content-contacts">
          <div class="footer__content-contacts-info">
            <h3
              class="footer__content-contacts-info-title"
              v-for="(item, index) in contactsInfo"
              :key="index"
              :id="item.id"
              @click="scrollToSection(item.id)"
            >
              {{ item.link[activeLanguage] }}
            </h3>
          </div>
          <div class="footer__content-contacts-number">
            <div class="footer__content-contacts-number-block">
              <font-awesome-icon
                class="footer__content-contacts-number-icon"
                icon="fa-solid fa-phone"
              />
              <a
                class="footer__content-contacts-number-link"
                href="tel: +998 90 980 53 00"
                >+998 90 980 53 00</a
              >
            </div>
            <div class="footer__content-contacts-number-block">
              <font-awesome-icon
                class="footer__content-contacts-number-icon"
                icon="fa-solid fa-envelope"
              />
              <a
                class="footer__content-contacts-number-link"
                href="mailto:hello@puzzle.uz "
                >hello@puzzle.uz</a
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="footer-company">
    <h4 class="footer-company-title" v-html="madeIn[activeLanguage]"></h4>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // contactTitle: { ru: "Контакты ", uz: `Aloqa`, en: "" },
      puzzleDescr: {
        ru: "Эффективный и надёжный партнер <br> в поиске и формировании маркетинговой <br> информации от потребителей <br> до торговых объектов.",
        uz: `Iste'molchilardan savdo obyektlarigacha bo'lgan marketingga oid ma'lumotlarni qidirish hamda shakllantirishda samarali va ishonchli hamkor.`,
        en: "<span>PUZZLE AGENCY</span> is the most <br> efficient and reliable partner in <br> the search for marketing <br> information.",
      },
      contactsInfo: [
        {
          link: { ru: "Наши услуги", uz: `Xizmatlar`, en: "Our services" },
          id: "#services",
        },
        {
          link: { ru: "O нас", uz: `biz haqimizda`, en: "About us" },
          id: "#about-us",
        },
        {
          link: {
            ru: "Нам доверяют",
            uz: `bizga ishonishadi`,
            en: "we are trusted",
          },
          id: "#trust",
        },
      ],
      madeIn: {
        ru: "сделано в <b>centris media</b>",
        uz: `<b>"centris media"</b>da tayyorlangan`,
        en: "made by <b>centris media</b>",
      },
    };
  },
  methods: {
    goToTop() {
      window.scrollTo(0, 0);
    },
    scrollTo(sectionId) {
      if (this.menuOpen) {
        this.menuOpen = false;
      }
      const section = document.querySelector(sectionId);
      const scrolDistance = section.offsetTop - 50;
      let distance = window.scrollY;
      const scroll = () => {
        if (scrolDistance > distance + 0.5 || scrolDistance < distance - 0.5) {
          distance -= (distance - scrolDistance) / 15;
          window.scrollTo(0, distance);
          this.timer = setTimeout(() => {
            scroll();
          }, 5);
        } else {
          window.scrollTo(0, scrolDistance);
          stop();
        }
      };
      const stop = () => {
        clearTimeout(this.timer);
        window.removeEventListener("wheel", stop);
      };
      stop();
      window.addEventListener("wheel", stop);
      scroll();
    },
    scrollToSection(sectionId) {
      if (this.$route.name != "home") {
        this.$router.push("/").then(() => {
          setTimeout(() => {
            this.scrollTo(sectionId);
          }, 300);
        });
      } else {
        this.scrollTo(sectionId);
      }
    },
  },
  computed: {
    activeLanguage() {
      return this.$store.getters.getLanguage;
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
