  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  background: #2a2135;
  @include adaptivPaddinglg(0, 0, 120, 100, 0, 0, 50, 50);
  position: relative;
  @media (max-width: 768px) {
    @include adaptivPaddingmd(0, 0, 50, 50, 0, 0, 40, 40);
  }
  &__content {
    width: 100%;
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &-puzzle {
      width: 30%;
      @include adaptivHeightlg(200, 150);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      @media (max-width: 768px) {
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
      }
      &-logo {
        @include adaptivWidthLg(130, 90);
        @include adaptivHeightlg(40, 30);
        @media (max-width: 768px) {
          @include adaptivWidthmd(200, 122);
          @include adaptivHeightmd(60, 40);
        }
      }
      &-descr {
        @include adaptiv-fontlg(16, 12);
        @include adaptiv-lineHeightlg(25, 18);
        color: #fff;
        font-family: "Montserrat-thin";
        span {
          font-family: "Montserrat-bold";
        }
        @media (max-width: 768px) {
          @include adaptiv-fontmd(24, 12);
          @include adaptiv-lineHeightmd(30, 18);
          @include adaptivMarginmd(0, 0, 40, 40, 0, 0, 20, 20);
          text-align: center;
        }
      }
    }
    &-contacts {
      width: 40%;
      @include adaptivHeightlg(200, 150);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 768px) {
        width: 100%;
        height: 100%;
        @include adaptivMarginmd(0, 0, 180, 0, 0, 0, 90, 0);
      }
      &-info {
        width: 100%;
        display: flex;
        justify-content: space-around;
        &-title {
          @include adaptiv-fontlg(16, 13);
          @include adaptiv-lineHeightlg(20, 17);
          color: #fff;
          font-family: "Montserrat-bold";
          text-transform: uppercase;
          @include adaptivMarginlg(0, 0, 10, 0, 0, 0, 5, 0);
          transition: 0.5s;
          cursor: pointer;
          &:hover {
            color: #dda864;
            transition: 0.5s;
          }
          @media (max-width: 768px) {
