            justify-content: center;
          }
        }

        &-icon {
          @include adaptiv-fontlg(18, 13);
          @include adaptiv-lineHeightlg(30, 20);
          @include adaptivMarginlg(0, 25, 0, 2, 0, 13, 0, 0);
          @media (max-width: 768px) {
            @include adaptiv-fontmd(20, 10);
